import {Column, IconButton, Row, Text} from "../nui/components";
import {Style} from "../nui/style";

export default class FileComponent extends Column {
	constructor() {
		super(
			{
				styles: {
					width: '130px',
					height: '160px'
				}
			}
		);
		const icon = new Column(
			{
				styles: {
					height: '100%',
				}
			}
		)
		this.add(icon)

		const info = new Row(
			{
				styles: {
				}
			}
		)
		info.add(
			new Text(
				{
					text: "File name",
					style: {
						width: 'auto',
						padding: '6px 10px',
						alignContent: 'center',
					}
				}
			)
		)
		info.add(
			new IconButton(
				{
					svgName: 'moreVertical'
				}
			)
		)
		this.add(info)
	}
}